.pagination {
    display: flex;
    gap: 30px;
    width: 100%;
    justify-content: end;

    &__element {
        background: white;
        color: $accent;
        font-size: 18px;
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        transition: all .5s ease;
        font-family: Verdana;

        &:hover {
            background: $accent;
            color: white;
            transition: all .3s ease;
        }
    }

    &__active {}
}