.post {
    display: flex;
    width: 100%;
    margin-top: 30px;
    background: rgba(41, 105, 200, 0.1);

    &__image {
        width: 33%;
    }

    &__text {
        width: 67%;
        margin-left: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding-right: 15px;
    }

    &__title {
        font-size: 24px;
    }

    &__descr {
        max-lines: 3;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        font-size: 18px;
    }

    &__link {
        text-align: right;
        color: $accent;
        text-decoration: underline;
    }
}